{\r\n  color: rgb(73, 142, 192);\r\n}\r\n.cell[data-num=\"2\"] {\r\n  color: rgb(75, 174, 103);\r\n}\r\n.cell[data-num=\"3\"] {\r\n  color: rgb(231, 117, 136);\r\n}\r\n.cell[data-num=\"4\"] {\r\n  color: rgb(150, 118, 237);\r\n}\r\n.cell[data-num=\"5\"] {\r\n  color: rgb(221, 137, 240);\r\n}\r\n.cell[data-num=\"6\"] {\r\n  color: rgb(54, 176, 176);\r\n}\r\n.cell[data-num=\"7\"] {\r\n  color: var(--darkest-color);\r\n}\r\n.cell[data-num=\"8\"] {\r\n  color: var(--backlight-color);\r\n}\r\n\r\n@media (max-width: 480px) {\r\n  .message {\r\n    font-size: 18px;\r\n  }\r\n\r\n  .last-games {\r\n    font-size: 14px;\r\n  }\r\n}\r\n",".settings {\r\n  &__title {\r\n    position: relative;\r\n    text-align: center;\r\n    margin: 20px auto 0;\r\n\r\n    &_range-input {\r\n      margin: 20px auto 5px;\r\n    }\r\n  }\r\n}\r\n",".game-field {\r\n  display: grid;\r\n  padding: 2px;\r\n  gap: 2px;\r\n  background: var(--bg-dark-color);\r\n  outline: none;\r\n  -webkit-transition: 0.2s;\r\n  transition: opacity 0.2s;\r\n  border-radius: 5px;\r\n  box-shadow: inset 0px 2px 5px var(--text-color),\r\n    0px 1px 2px var(--lightest-color);\r\n  width: fit-content;\r\n  margin: 20px auto;\r\n  align-self: flex-start;\r\n\r\n  &_size-10 {\r\n    grid-template-columns: repeat(10, auto);\r\n    border-radius: 8px;\r\n    padding: 4px;\r\n    gap: 4px;\r\n\r\n    & .cell {\r\n      width: 40px;\r\n      height: 40px;\r\n      border-radius: 8px;\r\n    }\r\n  }\r\n  &_size-15 {\r\n    grid-template-columns: repeat(15, auto);\r\n    border-radius: 3px;\r\n    padding: 3px;\r\n    gap: 3px;\r\n    & .cell {\r\n      width: 26px;\r\n      height: 26px;\r\n      border-radius: 3px;\r\n    }\r\n  }\r\n  &_size-25 {\r\n    grid-template-columns: repeat(25, auto);\r\n    border-radius: 3px;\r\n    padding: 2px;\r\n    gap: 2px;\r\n    & .cell {\r\n      width: 16px;\r\n      height: 16px;\r\n      border-radius: 2px;\r\n    }\r\n  }\r\n}\r\n\r\n@media (max-width: 480px) {\r\n  .game-field {\r\n    display: grid;\r\n    padding: 2px;\r\n    gap: 2px;\r\n    border-radius: 5px;\r\n    margin: 20px auto;\r\n\r\n    &_size-10 {\r\n      border-radius: 4px;\r\n      padding: 1px;\r\n      gap: 1px;\r\n\r\n      & .cell {\r\n        width: 30px;\r\n        height: 30px;\r\n        border-radius: 4px;\r\n      }\r\n    }\r\n    &_size-15 {\r\n      border-radius: 2px;\r\n      padding: 1px;\r\n      gap: 1px;\r\n      & .cell {\r\n        width: 18px;\r\n        height: 18px;\r\n        border-radius: 2px;\r\n      }\r\n    }\r\n    &_size-25 {\r\n      border-radius: 2px;\r\n      padding: 1px;\r\n      gap: 1px;\r\n      & .cell {\r\n        width: 18px;\r\n        height: 18px;\r\n        border-radius: 2px;\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n@media (min-width: 1480px) {\r\n  .game-field {\r\n    margin: 0;\r\n  }\r\n}\r\n",".footer {\n  display: flex;\n  flex-direction: column;\n  gap: 6px;\n  padding: 6px;\n}\n\n.last-games {\n  margin: 0 auto;\n  width: fit-content;\n\n  border-radius: 5px;\n  box-shadow: inset 0px 2px 5px var(--text-color),\n    0px 1px 2px var(--lightest-color);\n  padding: 6px;\n  background: var(--bg-dark-color);\n  gap: 16px;\n  display: flex;\n  flex-direction: column;\n\n  &__list {\n    gap: 6px;\n    display: flex;\n    flex-direction: column;\n    margin: 0;\n    padding: 0;\n  }\n\n  &__item {\n    display: flex;\n    gap: 15px;\n    justify-content: space-between;\n  }\n}\n\n@media (min-width: 1480px) {\n  .footer {\n    display: flex;\n    flex-direction: column-reverse;\n    justify-content: flex-end;\n  }\n}\n",".info {\r\n  display: flex;\r\n  gap: 10px;\r\n  flex-direction: column;\r\n  align-items: center;\r\n}\r\n\r\n.info__list {\r\n  display: flex;\r\n  gap: 15px;\r\n}\r\n\r\n.info__buttons {\r\n  display: flex;\r\n  gap: 20px;\r\n}\r\n\r\n@media (min-width: 1480px) {\r\n\r\n  .message {\r\n    max-width: 378px;\r\n  }\r\n}",".last-games__info {\n  max-width: 465px;\n\n  &-list {\n    display: flex;\n    flex-dir